<template>
  <div class="public">
    <header-content></header-content>
    <div class="public-content">
      <left :curid="curid" :isPull="isPull" @pull="pull"></left>
      <div class="right">
        <div class="right-content">
          <!-- 头部 -->
          <div class="title">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-shouye" />
            </svg>
            <p>
              你现在所在的位置是:
              <span>
                <router-link to="/activity">活动管理</router-link>
              </span> /
              <span>{{title}}</span>
            </p>
          </div>
          <!-- 详情 -->
          <div class="info">
            <el-form :model="info" :rules="rules" ref="info" label-width="180px" class="pro-info">
              <el-form-item label="活动标题：" prop="title">
                <el-input v-model="info.title"></el-input>
              </el-form-item>
              <el-form-item label="活动封面：" prop="img">
                <el-upload
                  class="avatar-uploader"
                  name="imgfile"
                  :action="RespondURL.url+'Goodsparticulars/uploadimg'"
                  :show-file-list="false"
                  :on-success="handleAvatarSuccess"
                >
                  <img v-if="imageUrl" :src="imageUrl" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                  <div v-if="imageUrl" class="delImg" @click.stop="imageUrl='';info.img=''">
                    <i class="el-icon-circle-close"></i>
                  </div>
                  <div v-if="imageUrl" class="revise">
                    替换
                    <i class="el-icon-copy-document"></i>
                  </div>
                </el-upload>
              </el-form-item>

              <el-form-item label="活动时间: " prop="starttime">
                <el-date-picker
                  type="date"
                  placeholder="发布时间"
                  v-model="info.starttime"
                  @change="dateChange"
                ></el-date-picker>
              </el-form-item>
              <el-form-item label="活动内容" prop="content" class="content">
                <div class="insert">
                  <el-upload
                    class="insert-uploader"
                    name="imgfile"
                    :action="RespondURL.url+'Goodsparticulars/uploadimg'"
                    :show-file-list="false"
                    :on-success="getImg"
                  >
                    <el-button type="primary">
                      <i class="el-icon-picture"></i>
                    </el-button>
                  </el-upload>
                </div>
                <div class="edit" contenteditable="true" ref="content" @blur="getContent"></div>
              </el-form-item>
            </el-form>

            <div class="btn">
              <el-button type="primary" @click="submitForm">立即发送</el-button>
              <el-button @click="resetForm">取消重置</el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import HeaderContent from "@/components/HeaderContent.vue";
import Left from "@/components/Left.vue";
export default {
  name: "ActivityAdd",
  components: { Left, HeaderContent },
  created() {
    if (this.$route.query.id) {
      this.title = "活动修改";
      let num = this.$route.query.id;
      this.id = num;
      this.$ajax
        .get(
          this.RespondURL.url + "ActivityContent/findActivityContentAll/ " + num
        )
        .then(data => {
          this.info = data.data.result;
          this.$refs.content.innerHTML = data.data.result.content;
          this.imageUrl = this.RespondURL.url + data.data.result.img;
        });
    } else {
      this.title = "添加活动";
    }
  },
  data() {
    return {
      curid: "4",
      title: "",
      id: "",
      info: {
        title: "",
        starttime: "",
        content: "",
        img: "",
        site: ""
      },
      rules: {
        name: [{ required: true, message: "请输入活动标题", trigger: "blur" }],
        time: [{ required: true, message: "请输入活动时间", trigger: "blur" }]
      },
      imageUrl: "",
      isPull: false
    };
  },
  methods: {
    // 下拉
    pull() {
      if (this.isPull) {
        this.isPull = false;
      } else {
        this.isPull = true;
      }
    },
    addHTML(html) {
      let sel = window.getSelection();
      if (sel.getRangeAt && sel.rangeCount) {
        let range = sel.getRangeAt(0);
        range.deleteContents();
        let el = document.createElement("div");
        el.innerHTML = html;
        let frag = document.createDocumentFragment(),
          node,
          lastNode;
        while ((node = el.firstChild)) {
          lastNode = frag.appendChild(node);
        }
        range.insertNode(frag);
        if (lastNode) {
          range = range.cloneRange();
          range.setStartAfter(lastNode);
          range.collapse(true);
          sel.removeAllRanges();
          sel.addRange(range);
        }
      }
    },
    // 得到活动内容
    getContent() {
      this.info.content = this.$refs.content.innerHTML;
    },
    // 插入图片
    getImg(res) {
      let str = `<img src="${this.RespondURL.url}${res.result}" alt="">`;
      this.addHTML(str);
    },
    handleAvatarSuccess(res) {
      this.imageUrl = this.RespondURL.url + res.result;
      this.info.img = res.result;
    },
    // 时间
    dateChange(data) {
      let date = this.$moment(data).format("YYYY-MM-DD");
      this.info.starttime = date;
    },
    submitForm() {
      this.$refs.info.validate(valid => {
        if (valid) {
          let repURL = "";
          if (this.id == "") {
            repURL =
              this.RespondURL.url + "ActivityContent/insertActivityContent";
          } else {
            this.info.id = this.id[0];
            repURL =
              this.RespondURL.url + "ActivityContent/updateActivityContent";
          }
          this.$ajax.post(repURL, this.info).then(data => {
            this.$alert(data.data.describe, "提示", {
              confirmButtonText: "确定",
              callback: () => {
                if (data.data.flag) {
                  this.$router.push("/activity");
                }
              }
            });
          });
        } else {
          this.$alert("发送失败，请内容填写完整", "提示", {
            confirmButtonText: "确定",
            callback: () => {}
          });
          return false;
        }
      });
    },
    resetForm() {
      this.info = {
        title: "",
        starttime: "",
        content: "",
        img: "",
        site: ""
      };
      this.$refs.content.innerHTML = "";
      this.imageUrl = ""
    }
  }
};
</script>
<style lang="less">
.content {
  text-align: left;
  .el-form-item__content {
    line-height: 25px;
    padding: 0 20px;
    .insert {
      background: #409eff;
      border-radius: 5px 5px 0 0;
      width: 80%;
      min-width: 500px;
      padding: 0 30px;
      .el-button {
        line-height: 30px;
        padding: 0;
        margin: 0;
        background: #409eff;
        border: none;
        font-size: 30px;
        color: white;
      }
    }
    .edit {
      width: 80%;
      min-width: 500px;
      height: 500px;
      overflow-y: scroll;
      padding: 20px;
      border: 1px solid rgb(196, 195, 195);
      outline: none;
      text-indent: 28px;
      img {
        max-width: 100%;
        display: block;
      }
    }
  }
}
</style>